<template>
    <div class="reports_container">
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!-- 2. EChartsDom -->
            <div id="main" style="width: 750px;height:400px;"></div>
        </el-card>
        <div>
            <h3>:</h3>
            <img
                src="https://echarts.apache.org/zh/documents/asset/img/basic-concepts-overview/series-all-a.jpg"
                alt="echart"
            />
            <pre>
             option  json 

            

            

            title: {
                text: ' ECharts '
            }
            

            

            tooltip: {},
            

            (symbol)

            legend: {
                data: [{
                    name: '1',
                    // 
                    icon: 'circle',
                    // 
                    textStyle: {
                        color: 'red'
                    }
                }]
            }
            X 

             X :

            xAxis: {
                data: ["","","","","",""]
            }
            Y 

             Y 

            yAxis: {}
            

             type :

            series: [{
                name: '',  // 
                type: 'bar',  // 
                data: [5, 20, 36, 10, 10, 20]  // 
            }]
             type 

            type: 'bar'/
            type: 'line'/
            type: 'pie'
            type: 'scatter'
            type: 'effectScatter'
            type: 'radar'
            type: 'tree'
            type: 'treemap'
            type: 'sunburst'
            type: 'boxplot'
            type: 'candlestick'K
            type: 'heatmap'
            type: 'map'
            type: 'parallel'
            type: 'lines'
            type: 'graph'
            type: 'sankey'
            type: 'funnel'
            type: 'gauge'
            type: 'pictorialBar'
            type: 'themeRiver'
            type: 'custom'
            </pre>
        </div>
    </div>
</template>

<script>
// domecharts
import echarts from "echarts";
import _ from "lodash";
export default {
    name: "Reports",
    components: {},
    props: {},
    data() {
        return {
            options: {
                title: {
                    text: ""
                },
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "cross",
                        label: {
                            backgroundColor: "#E9EEF3"
                        }
                    }
                },
                xAxis: [
                    {
                        boundaryGap: false
                    }
                ],
                yAxis: [{ type: "value" }],
                grid: {
                    left: "13%",
                    right: "4%",
                    bottom: "3%",
                    containLabel: true
                }
            }
        };
    },
    watch: {},
    computed: {},
    methods: {},
    created() {},
    mounted() {
        var myChart = echarts.init(document.getElementById("main"));

        const defaultOptions = {
            title: {
                text: "ECharts "
            },
            tooltip: {},
            legend: {
                data: [""]
            },
            xAxis: {
                data: ["", "", "", "", "", ""]
            },
            yAxis: {},
            series: [
                {
                    name: "",
                    type: "line",
                    data: [5, 14, 36, 10, 10, 20]
                }
            ]
        };

        // 
        this.$http.get("/reports/1").then(rest => {
            const result = rest.data;
            if (result.meta.status == 200) {
                this.options = _.merge(this.options, result.data);
                //,lodash merge
                //console.log(this.options);
                // 
                myChart.setOption(this.options);
            } else {
                this.$message.error("");
                myChart.setOption(defaultOptions);
            }
        });
    }
};
</script>
<style lang="scss" scoped></style>
